  <h1>{{firstname}} {{lastname}}</h1>
  <h3>{{age}}</h3>

  <button @click="plus">เพิ่มอายุ</button>
  <button @click.ctrl="pluss(5)">เพิ่มอายุ</button>
  <button @click="minus">ลดอายุ</button>
<button @click="minus(10)">ลดอายุ</button>
  <form>

    <label for="">ป้อนชื่อจริง</label>
    <input type="text" v-on:input="setname">
    <br>
    <label for="">ป้อนชื่อนามสกุล</label>
    <input type="text" v-on:input="setlname">
    <br>
    <label for="">ป้อนชื่อเล่น</label>
    <input type="text" v-on:input="setnickname">
    <br>
    <label for="">ป้อนชื่ออายุ</label>
    <input type="text" v-on:input="setagee">
    <br>
    <label for="">ป้อนชื่อวัน/เดือน/ปีเกิด</label>
    <input type="text" v-on:input="setdate">
    <br>
    <label for="">ป้อนสาขา</label>
    <input type="text" v-on:input="setmajor">
    <br>
    <label for="">ป้อนห้อง</label>
    <input type="text" v-on:input="setrooms">
    <br>
    <label for="">ป้อนคติประจำใจ</label>
    <input type="text" v-on:input="setmotto">
    <br>
    <label for="">ป้อนสถานที่เรียนต่อ</label>
    <input type="text" v-on:input="setstudying">
    <br>
    <label for="">ป้อนวิชาที่ไม่ชอบ</label>
    <input type="text" v-on:input="setsubject">
    <br>
  </form>

  <h2>แสดงชื่อจริง : {{name}}</h2>
  <h2>แสดงชื่อนามสกุล : {{lname}}</h2>
  <h2>แสดงชื่อเล่น : {{nickname}}</h2>
  <h2>แสดงชื่ออายุ : {{agee}}</h2>
  <h2>แสดงชื่อวัน/เดือน/ปีเกิด : {{data}}</h2>
  <h2>แสดงสาขา : {{major}}</h2>
  <h2>แสดงห้อง : {{rooms}}</h2>
  <h2>แสดงคติประจำใจ : {{motto}}</h2>
  <h2>แสดงสถานที่เรียนต่อ : {{studying}}</h2>
  <h2>แสดงวิชาที่ไม่ชอบ : {{subject}}</h2>

