<script>

export default {
  name: 'App',
  data(){
    return{
      firstname : "ธนายุทธ",
      lastname : "จริงมาก",
      age : 20,
      name:"",
      lname:"",
      nickname:"",
      agee:"",
      data:"",
      major:"",
      rooms:"",
      motto:"",
      subject:"",
    }
  },
  methods:{
    showdata(){
      alert(this.firstname)
    },
    plus(){
      this.age++
    },
    pluss(val){
      this.age+=val
    },
    minus(val){
      this.age-=val
    },
    setname(event){
      // console.log(event.target.value);
      this.name = event.target.value
    },
    setlname(event){
      // console.log(event.target.value);
      this.lname = event.target.value
    },
    setnickname(event){
      // console.log(event.target.value);
      this.nickname = event.target.value
    },
    setagee(event){
      // console.log(event.target.value);
      this.agee = event.target.value
    },
    setdate(event){
      // console.log(event.target.value);
      this.data = event.target.value
    },
    setmajor(event){
      // console.log(event.target.value);
      this.major = event.target.value
    },
    setrooms(event){
      // console.log(event.target.value);
      this.rooms = event.target.value
    },
    setmotto(event){
      // console.log(event.target.value);
      this.motto = event.target.value
    },
    setstudying (event){
      // console.log(event.target.value);
      this.studying = event.target.value
    },
    setsubject(event){
      // console.log(event.target.value);
      this.subject = event.target.value
    },
  }
};
</script>

